html {
  font-size: 100px; }

body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

/* reset css */

html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, a, cite, img, sub, sup, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  border: 0;
  margin: 0;
  padding: 0;
  font-weight: normal;
  vertical-align: baseline; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block; }

button {
  outline: none; }

ol, ul {
  margin: 0;
  padding: 0; }

ol, ul, li {
  list-style: none; }

i, em {
  font-style: normal; }

blockquote {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

a {
  outline: none;
  text-decoration: none; }

input, textarea {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  outline: none;
  border: 0;
  margin: 0;
  padding: 0; }

select, option {
  font-family: inherit; }

*::before,
*::after,
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.clearfix::after {
  content: "";
  display: table;
  clear: both;
  float: none;
  *zoom: 1; }

html {
  font-size: 100px;
  height: 100%; }

body {
  height: 100%; }

main {
  position: relative;
  height: 100%;
  padding: 50px 0 0; }

main > .content {
    position: relative;
    height: 100%; }

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background-color: #8bc34a;
  z-index: 9; }

.sidebar {
  position: relative;
  width: 1.8rem;
  height: 100%;
  float: left;
  background: #8bc34a;
  -webkit-transition: width 340ms;
  transition: width 340ms;
  z-index: 9; }

.sidebar .control {
    display: block;
    height: 30px;
    background: rgba(255, 255, 255, 0.3);
    overflow: hidden;
    cursor: pointer; }

.sidebar .item-child {
    white-space: nowrap;
    font-size: 0.14rem;
    line-height: 40px;
    height: 40px;
    color: #fff;
    cursor: pointer; }

.sidebar .item-child .icon {
      display: block;
      float: left;
      width: 40px;
      height: 100%;
      padding-left: 10px; }

.sidebar .item-child > span {
      position: relative;
      display: inline-block;
      opacity: 1;
      -webkit-transition: all 340ms;
      transition: all 340ms; }

.sidebar .item-child > span::before {
        content: '';
        position: absolute;
        top: 15px;
        left: -6px;
        width: 5px;
        height: 10px;
        border: solid transparent;
        border-width: 5px 6px 5px 0;
        -webkit-transition: border 340ms;
        transition: border 340ms; }

.container {
  overflow: hidden; }

.kakushi {
  position: absolute;
  z-index: -1;
  clip: rect(0, 0, 0, 0);
  -webkit-filter: opacity(0);
          filter: opacity(0); }

.icon-menu {
  position: relative;
  display: block;
  width: 18px;
  height: 2px;
  border-radius: 1px;
  margin: 14px auto;
  background: #fff;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-transition: -webkit-transform 340ms;
  transition: -webkit-transform 340ms;
  transition: transform 340ms;
  transition: transform 340ms, -webkit-transform 340ms; }

.icon-menu::before, .icon-menu::after {
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    background: inherit; }

.icon-menu::before {
    top: -5px; }

.icon-menu::after {
    bottom: -5px; }

.sidebar-item {
  display: block;
  font-size: 0.14rem;
  height: 40px;
  line-height: 40px;
  padding-left: 10px;
  background: rgba(255, 255, 255, 0.2);
  white-space: nowrap;
  color: #fff;
  cursor: pointer; }

.sidebar-item::before {
    content: '';
    display: block;
    float: left;
    width: 4px;
    height: 8px;
    margin: 16px 13px;
    border: solid transparent;
    border-width: 4px 0 4px 4px;
    border-left-color: #fff;
    -webkit-transition: -webkit-transform 340ms;
    transition: -webkit-transform 340ms;
    transition: transform 340ms;
    transition: transform 340ms, -webkit-transform 340ms; }

.sidebar-item > span {
    position: relative;
    display: inline-block;
    opacity: 1;
    -webkit-transition: all 340ms;
    transition: all 340ms; }

.sidebar-item > span::before {
      content: '';
      position: absolute;
      top: 15px;
      left: -6px;
      width: 5px;
      height: 10px;
      border: solid transparent;
      border-width: 5px 6px 5px 0;
      -webkit-transition: border 340ms;
      transition: border 340ms; }

input#sidebar_control:checked + .sidebar {
  width: 50px; }

input#sidebar_control:checked + .sidebar .icon-menu {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }

input#sidebar_control:checked + .sidebar .item-child > span,
  input#sidebar_control:checked + .sidebar .sidebar-item > span {
    opacity: 0;
    border-radius: 4px;
    padding: 0 1.5em; }

input#sidebar_control:checked + .sidebar .item-child:hover > span,
  input#sidebar_control:checked + .sidebar .sidebar-item:hover > span {
    opacity: 1;
    background: #8bc34a;
    margin-left: 20px; }

input#sidebar_control:checked + .sidebar .item-child:hover > span::before,
    input#sidebar_control:checked + .sidebar .sidebar-item:hover > span::before {
      border-right-color: #8bc34a; }

input.sidebar-control:checked + .item .sidebar-item::before {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg); }
